* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


html {
    font-size: 62.5%;
    font-family: 'Roboto', sans-serif;
    scroll-behavior: smooth;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

/*-------------- Navbar styles-------------- */

.navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem 1.5rem;
    border-bottom: 1px solid rgba(85, 85, 85, 0.452);
    width: 100%;
    height: 65px;
}

.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
}

.nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin: 0%;
}


.nav-link{
    font-size: 1.8rem;
    font-weight: 400;
    color: #475569;
}

.nav-link:hover{
    color: #482ff7;
}

.nav-logo.nav-link {
    font-size: 2.1rem;
    font-weight: 500;
    color: #482ff7; 
    text-decoration: none;
}   

@media only screen and (max-width: 768px) {
    .nav-menu {
        position: absolute;
        left: -100%;
        top: 5.5rem;
        -webkit-flex-direction: column;
                flex-direction: column;
        background-color: #fff;
        width: 100%;
        text-align: center;
        transition: 0.3s;   
        box-shadow:
            0 10px 27px rgba(0, 0, 0, 0.05);
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 2.5rem 0;
    }

    .hamburger {
        display: block;
        cursor: pointer;
    }

    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }

}

.nav-icons{
    margin-left: 8px;
}
.navbar ul,
.navbar ul li
{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center ;
            align-items: center ;
}
.navbar li a{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: auto;
    margin: 0px 8px;
}


.navbar-default{
  transition:500ms ease;
  background:transparent;
}
.navbar-default.scrolled{
  background:#fff;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.192);   
}


/*---------------------Home Page Styles-------------------*/

.component-size{
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    
}

.home_bg{
    background: url(/rent-n-ride/static/media/homebg.a9038ea5.jpg) no-repeat center; 
    background-size: cover;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.home-title{
    font-size: 3rem;
    color: #000;
    font-weight: bolder;

}
.title-color{
    color: rgb(0, 153, 46);
}

.form-container{
    background: rgba(255, 255, 255, 0.425);
    padding: 0px 20px 40px;
}


.form-body{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
